import React from 'react';

import Button from '@material-ui/core/Button'; 
import { Container, Typography, Paper, Grid } from '@material-ui/core';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    mainFeaturePost:{
        position: 'relative',
        color: theme.palette.common.white,
        marginBottom: theme.spacing(4),
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center"
    },
    overlay:{
        position: 'absolute',
        top: 0,
        bottom: 0,
        right: 0,
        left: 0,
        backgroundOverlay: "rgba(0, 0, 0, .3)"
    },
    mainFeaturePostContent:{
        position: 'relative',
        padding: theme.spacing(8),  
        marginTop: theme.spacing(3)
    }
}))


function BlockBigPicture() {  
    const classes = useStyles();
    
    return (
        <>
            <Paper className={classes.mainFeaturePost} style={{backgroundImage: `url(https://source.unsplash.com/random)`}} >
                <Container fixed > 
                    <div className={classes.overlay}></div>
                    <Grid container >  
                         <Grid item md={6} > 
                            <div className={classes.mainFeaturePostContent}>
                                <Typography
                                component="h1" 
                                variant="h3" 
                                color="inherit" 
                                gutterBottom >
                                    My beautiful project
                                </Typography>
                                <Typography 
                                variant="h6" 
                                color="inherit" 
                                paragraph >
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit. 
                                    Perferendis cum nemo voluptatem autem dolore natus eveniet officiis, 
                                    labore dignissimos tempore voluptatum repellat velit voluptate 
                                    voluptatibus!
                                </Typography>
                                <Button variant="contained" color="secondary">
                                    Learn more
                                </Button>
                            </div>
                         </Grid>
                    </Grid>
                </Container>
            </Paper>
        </>
    )
}

export default BlockBigPicture;