import React from 'react';

import Button from '@material-ui/core/Button'; 
import { Container, Typography, Grid, Card, CardMedia, CardContent, CardActions } from '@material-ui/core';

import LayerIcon from '@material-ui/icons/Layers';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    cardMedia:{
        paddingTop: "56.25%"
    },
    cardContent:{
        flexGrow: 1
    },
    cardGrid:{
        marginTop: theme.spacing(4) 
    },
    distanceBetweenCards:{
       padding: "15px"
    }
}))

const cards = [1, 2, 3, 4, 5, 6, 7, 8, 9];

function Cards() {  
    const classes = useStyles();
    
    return (
        <>
            <Container className={classes.cardGrid} maxWidth="md" >
                <Grid container spaсing={4} >
                    {cards.map(card => (
                        <Grid item key={card} xs={12} sm={6} md={4} className={classes.distanceBetweenCards}>
                            <Card className={classes.card}>
                                <CardMedia className={classes.cardMedia} 
                                image="https://source.unsplash.com/random"
                                title="image title" 
                                />
                                <CardContent className={classes.cardContent}>
                                    <Typography variant="h5" gutterBottom>
                                        Blog Post
                                    </Typography>
                                    <Typography >
                                        Lorem ipsum dolor sit amet consectetur adipisicing elit.
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button size="small" color="primary">
                                        View
                                    </Button>
                                    <Button size="small" color="primary">
                                        Edit
                                    </Button>
                                    <LayerIcon />
                                    <PlayCircleFilledIcon />
                                </CardActions>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Container>
        </>
    )
}

export default Cards;