import React from 'react';

import Button from '@material-ui/core/Button'; 
import { AppBar, Box, Container, IconButton, Toolbar, Typography, Dialog, DialogTitle, DialogContent,
    DialogContentText, TextField, DialogActions } from '@material-ui/core';

import MenuIcon from '@material-ui/icons/Menu';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    menuButton:{
        marginRight: theme.spacing(1)
    },
    title:{
        flexGrow: 1    
    }
}))


function Header() {  
    const classes = useStyles();
    const [open, setOpen] =  React.useState(false)

    const handleClickOpen = () => {
        setOpen(true)
    }

    const handleClose = () => {
        setOpen(false)
    }
    
    return (
        <>
        <AppBar position="fixed"> 
            <Container fixed>
                <Toolbar>                             
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                        <MenuIcon></MenuIcon>
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>Алексей Болдарев</Typography> 
                    <Box mr={3}>                     
                        <Button color="inherit" variant="outlined" onClick={handleClickOpen}>Log In</Button>
                        <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title" >
                            <DialogTitle id="form-dialog-title" >Log In</DialogTitle>
                            <DialogContent>
                                <DialogContentText>Log in to see videous</DialogContentText>
                                <TextField 
                                    autoFocus
                                    margin="dense"
                                    id="name"
                                    label="Email Adress"
                                    type="email"
                                    fullWidth
                                />
                                <TextField 
                                    autoFocus
                                    margin="dense"
                                    id="pass"
                                    label="Password"
                                    type="password"
                                    fullWidth
                                />
                            </DialogContent>
                            <DialogActions>
                                <Button onClick={handleClose} color="primary">Cancel</Button>
                                <Button onClick={handleClose} color="primary">Log in</Button>
                            </DialogActions>
                        </Dialog>
                    </Box>
                    <Button color="secondary" variant="contained" >Sign Up</Button>
                </Toolbar>
            </Container>
        </AppBar>
        </>
    )
}

export default Header;